import React from 'react'
import './Services.css'
import {BiCheck} from 'react-icons/bi'

const Services = () => {
return (
  <section id='services'>
    <h5>What I Offer</h5>
    <h2>Services</h2>

    <div className="container services__container">
      <article className="service">
        <div className="service__head">
          <h3>UI/UX Design</h3> 
        </div>

        <ul className="service__list">
          <li><BiCheck  className='service__list-icon' />
            <p>Design graphic user interface elements, like menus, tabs and widgets</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Build page navigation buttons and search fields</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Conduct layout adjustments based on user feedback</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Adhere to style standards on fonts, colors and images</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Identify and troubleshoot UX problems (e.g. responsiveness)</p>
          </li>
        </ul>
      </article>

                  {/* =============== END OF UI/UX ================= */}

      <article className="service">
        <div className="service__head">
          <h3>Web Developement</h3> 
        </div>

        <ul className="service__list">
          <li><BiCheck  className='service__list-icon' />
            <p>Directing or performing Website updates.</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Back up files from Web sites to local directories for recovery</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Using scripting or authoring languages, management tools, content creation tools, applications, and digital media.</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Determining user needs by analyzing technical requirements.</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Conferring with teams to resolve conflicts, prioritize needs, develop content criteria, or choose solutions.</p>
          </li>
        </ul>
      </article>

                  {/* =============== END OF Web Developement ================= */}

      <article className="service">
        <div className="service__head">
          <h3>Content Creation</h3> 
        </div>

        <ul className="service__list">
          <li><BiCheck  className='service__list-icon' />
            <p>Writing, reviewing, editing, and updating content for company websites, blogs, marketing materials, and similar platforms.</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Using social media to engage consumers, respond to questions or complaints, and to promote company initiatives.</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Suggesting new ways to promote company offerings and to reach consumers.</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Assisting the creative team with the design of promotional materials.</p>
          </li>
          <li><BiCheck  className='service__list-icon' />
            <p>Monitoring social media and company website metrics.</p>
          </li>
        </ul>
      </article>
      
    </div>
  </section>
)
}

export default Services