import React from 'react'
import './About.css'
import ME from '../../assets/me.jpg'
import {BsAward} from 'react-icons/bs'
import {FiUsers} from 'react-icons/fi'
import {VscFileSubmodule} from 'react-icons/vsc'



const About = () => {
return (
    <section id='about'>
        <h5>Get To Know</h5>
        <h2>About Me</h2>

        <div className="container about__container">
          <div className="about__me">
            <div className="about__me-img">
              <img src={ME} alt="" />
            </div>
          </div>

          <div className="about__content">
            <div className="about__cards">
              <article className='about__card'>
                <BsAward className='about__icon'/>
                <h5>Experience</h5>
                <small> +3 Years Of Experience </small>
              </article>

              <article className='about__card'>
                <FiUsers className='about__icon'/>
                <h5>Clients</h5>
                <small> +300 Worldwild</small>
              </article>

              <article className='about__card'>
                <VscFileSubmodule className='about__icon'/>
                <h5>Projects</h5>
                <small> +20 Completed </small>
              </article>
            </div>

            <p>Étudiant cycle master, titulaire d'une licence en développement informatique. Étant bilingue et à la recherche d'un nouveau défi professionnel en qualité de développeur web.</p>

            <a href="#contact" className='btn btn-primary'>Let's Talk</a>
          </div>
        </div>
    </section>
)
}

export default About