import React from 'react'
import './Contact.css'
import {AiOutlineMail} from 'react-icons/ai'
import {BsInstagram, BsWhatsapp} from 'react-icons/bs'
import  { useRef } from 'react';
import emailjs from 'emailjs-com';

const Contact = () => {
const form=useRef();

const sendEmail = (e) => {
  e.preventDefault();

  emailjs.sendForm('service_qnj3trf', 'template_rxgzcpk', form.current, 'hUi2WjbsJlz48GxW_')
    .then((result) => {
        console.log(result.text);
    }, (error) => {
        console.log(error.text);
    });
    e.target.reset()
};

return (
  <section id='contact'>
    <h5>Get In Touch</h5>
    <h2>Contact Me</h2>

    <div className="container contact__container">
      <div className="contact__options">
        <article className="contact__option">
          <AiOutlineMail className='contact__option-icon' />
          <h4>Email</h4>
          <h5>temmaramine930@gmail.com</h5>
          <a href="mailto:temmaramine930@gmail.com" target="_blank"> Send a message </a>
        </article>
        <article className="contact__option">
          <BsInstagram  className='contact__option-icon'/>
          <h4>Instagram</h4>
          <h5> Med Amine Temmar </h5>
          <a href="https://www.instagram.com/med_amine_temmar/" target="_blank">Send a message</a>
        </article>
        <article className="contact__option">
          <BsWhatsapp className='contact__option-icon' />
          <h4>WhatsApp</h4>
          <h5>+212680700***</h5>
          <a href="https://api.whatsapp.com/send?phone=+212680700799" target="_blank">Send a message</a>
        </article>
        </div>

        {/* End of Contact Options */}

        <form ref={form} onSubmit={sendEmail}>
          <input type="text" name='name' placeholder='Type Your Full Name' required />
          <input type="email" name='email' placeholder='Your Email' required />
          <textarea name="message" placeholder='Type Your Message ' required rows="7"></textarea>
          <button type='submit' className='btn btn-primary'>Send The Message</button>
        </form>
      </div>
    
  </section>
)
}

export default Contact