import React from "react";
import "./Portfolio.css"
import blog1 from '../../assets/prj1.jpeg'
import blog2 from '../../assets/prj2.webp'
import blog3 from '../../assets/prj3.webp'


function Blog() {
  return (
    <section className="blog" id="blog">
      <div className="max-width">
        <div className="sub-title"> My Portfolio </div>
        <div className="container-blog">
          <div className="blog-content">
            <div className="img">
              <img src={blog1} alt="" />
            </div>
          </div>
          <div className="blog-content">
            <div className="img">
              <img src={blog2} alt="" />
            </div>
          </div>
          <div className="blog-content">
            <div className="img">
              <img src={blog3} alt="" />
            </div>
          </div>
          </div>
        </div>
    
    </section>
  );
}

export default Blog;